import { Injectable } from '@angular/core';
import * as auth from 'firebase/auth';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import firebase from 'firebase/compat';
import { from, map, Observable } from 'rxjs';
import { LocalStorageService } from '@app/services/local-storage.service';

enum Auth {
  User = 'user',
}

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  constructor(
    private ngFirebaseAuth: AngularFireAuth,
    private ngLocalStorage: LocalStorageService
  ) {}

  SignIn() {
    return this.AuthLogin(new auth.GoogleAuthProvider());
  }

  SignOut(): Observable<void> {
    return from(this.ngFirebaseAuth.signOut());
  }

  AuthState() {
    return this.ngFirebaseAuth.authState.pipe(
      map(user => {
        if (user) {
          this.ngLocalStorage.save(Auth.User, user.refreshToken);
          return true;
        } else {
          this.ngLocalStorage.remove(Auth.User);
          return false;
        }
      })
    );
  }

  private AuthLogin(
    provider: firebase.auth.AuthProvider
  ): Observable<firebase.auth.UserCredential> {
    return from(this.ngFirebaseAuth.signInWithPopup(provider));
  }
}
